.footer{
    background: #a2b9bf;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.footer-container{
    padding: 20px 0px;
    color: black;

}
.copyright-section{
    background: #2B3B4C;
    padding: 10px 0px;
    color: #b5b5b5;

}

.copyright-section p{
    margin: 0px;
}

.copyright-section span{
    color: white;
}









@media only screen and (max-width: 767px) {
    .copyright-section {
        font-size: 11px;
    }

}

.email-isnt-acctive{
    height: 28px;
    background: red;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    /* text-align: center; */
    color: white;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.email-isnt-acctive .fa{
    margin: 0 10px;
}

.email-isnt-acctive-close{
    position: absolute;
    right:10px;
    top:50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    z-index: 2;
    cursor: pointer;
}

.email-isnt-acctive-close .fa{
    font-size: 18px;
}

